import React, { useEffect, useState, useRef } from "react";



import { getStore, gen16CodeId, ObjectKeys,  parseDate, updStore, _monthNames, copyToClipboard, parseDateMMDDYYYY, fetchPostH, getUrlServerSide } from "../../../stores/Utils";



import "../component/dashboard.css"
import { SvgIcon_Cmpt } from "../../../components";
import ChildrensDashboard from "../Childrens/ChildrensDashboard";
import SiblingsDashboard from "./SiblingsDashboard";


const Svg = SvgIcon_Cmpt();






const ParentsDashboard  = ({ openUpd,   close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(1);


	const updObs =  ()=>{
		set_obs(gen16CodeId())
	}
	


	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			// let user = {...getStore("upd_client")};
			set_obs(gen16CodeId())
		}
	});

	


	return (
		<div className=" ">
		
			<div className={"detailClient"}>
				<div className="_dsplFlx ">
					<p className="form_title">
						Padres
					</p>
					<div className="flexSpace"/>
				</div>
			</div>
				
			<div className="_dsplFlx  spaceAround flxWrp parentsInfo_box" >
				{getStore("upd_client")?.["fatherInfo"] ?<ParentsItem item={getStore("upd_client")?.["fatherInfo"]} title={"Padre"}/>:null}
				{getStore("upd_client")?.["motherInfo"] ?<ParentsItem item={getStore("upd_client")?.["motherInfo"]} title={"Madre"}/>:null}
			</div>
			<SiblingsDashboard />
			<ChildrensDashboard />

		</div>
	);
};

export default ParentsDashboard;







const ParentsItem  = ({ item, title }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const updObs  =  (v)  => {
		set_obs( gen16CodeId());
	};

	const copyToClpb  =  (v)  => {
		copyToClipboard( v,updObs);
	};


	return (

		<div className="_dsplFlx ">
			<div className="flexSpace"/>
			<div className={`card_address modern `}  >
				<div className="_dsplFlx ">
					<div className="title_card">
						{title}
					</div>
					<div className="flexSpace"/>
				</div>
				<div className="body_card">
					<div className="_dsplFlx city_state">
						<div className="h4_gsans"  onClick={()=>copyToClpb(item.firstName)}>
							{item?.firstName}
						</div>
						<div className="h4_gsans"  onClick={()=>copyToClpb(item.middleName)}>
							{item?.middleName}
						</div>
						<div className="h4_gsans" onClick={()=>copyToClpb(item.lastName)}>
							{item.lastName}
						</div>
					</div>
				</div>
				<div className="body_card">
					<div className="_dsplFlx city_state">
						{item?.placeOfBirth?.city?
						<div className="h4_gsans"  onClick={()=>copyToClpb(item?.placeOfBirth?.city)}>
							{item?.placeOfBirth?.city}
						</div>
						:null}
						{item?.placeOfBirth?.state?
						<div className="h4_gsans" onClick={()=>copyToClpb(item?.placeOfBirth?.state)}>
							{item?.placeOfBirth?.state}
						</div>
						:null}
					</div>
				</div>
			</div>
			<div className="flexSpace"/>
		</div>
	)
}

