import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId } from "../../../stores/Utils";

import {  TextInput_Cmpt } from "../../../components";

import "../component/dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";


const TextInput = TextInput_Cmpt();



const frmNm = "new_jobs"

const AddChildrens  = ({ data, confirm, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const addSave  = async ()  => {
		let ffm = getStore(frmNm);
		confirm && confirm(ffm);
		close();
	};





	const close  = ()  => {
		data?.modalID && CloseModal({id:data?.modalID})
	}


	
	

	return (
		<div style={{
			maxHeight: "80vh",
			width: "690px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar Trabajo
				</p>
				<div className="flexSpace"/>
				
			</div>
			
			
			<div className="_dsplFlx spaceAround flxWrp">
				<TextInput  
					init={getStore(frmNm)?.["country"]} 
					label={"Pais"} 
					width={260} 
					updChanges={(e)=>updFrm("country",e)}
				/>
				<TextInput 
					init={getStore(frmNm)?.["employerName"]} 
					label={"Empleador"} 
					width={200} 
					updChanges={(e)=>updFrm("employerName",e)}
				/>
				<TextInput 
					init={getStore(frmNm)?.["occupation"]} 
					label={"Ocupacion"} 
					width={520} 
					updChanges={(e)=>updFrm("occupation",e)}
				/>
			</div>
			<div className="_dsplFlx spaceAround flxWrp "> 
				
				
				<TextInput 
					init={getStore(frmNm)?.["addressLineOne"]} 
					label={"Calle "} 
					width={520} 
					updChanges={(e)=>updFrm("addressLineOne",e)}
				/>
				<TextInput 
					init={getStore(frmNm)?.["city"]} 
					label={"Ciudad o Municipio"} 
					width={200} 
					updChanges={(e)=>updFrm("city",e)}
				/>
				<TextInput  
					init={getStore(frmNm)?.["state"]} 
					label={"Estado o Provincia"} 
					width={160} 
					updChanges={(e)=>updFrm("state",e)}
				/>
				<TextInput  
					init={getStore(frmNm)?.["zipcode"]} 
					label={"Codigo Postal"} 
					width={60} 
					updChanges={(e)=>updFrm("zipcode",e)}
				/>
			</div>

			<div className="_dsplFlx spaceAround flxWrp" style={{marginTop:14}}> 
				<TextInput  
					init={getStore(frmNm)?.["fromDate"]?.["month"]} 
					label={"Desde (Month)"} 
					width={60} 
					updChanges={(e)=>updFrm("month",e, "fromDate")}
				/>
				<TextInput  
					init={getStore(frmNm)?.["fromDate"]?.["year"]} 
					label={"Desde (Year)"} 
					width={60} 
					updChanges={(e)=>updFrm("year",e, "fromDate")}
				/>
				<TextInput  
					init={getStore(frmNm)?.["toDate"]?.["month"]} 
					label={"Hasta (Month)"} 
					width={60} 
					updChanges={(e)=>updFrm("month",e, "toDate")}
				/>
				<TextInput  
					init={getStore(frmNm)?.["toDate"]?.["year"]} 
					label={"Hasta (Year)"} 
					width={60} 
					updChanges={(e)=>updFrm("year",e, "toDate")}
				/>
			</div>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="card">
					<button onClick={addSave}>
						Guardar
					</button>
				</div>

			</div>
		

			
		</div>
	);
};

export default AddChildrens;

