import { qryStore, getStore, fetchEncryptStream, updStore, convertObj2Array, callbackOnMemory } from "../../../stores/Utils";



export const getall_notaryAgent = async (cb) => {
    // notaryAgentId
    let bdyq2 = {...getStore(qryStore())?.all_notaryAgn};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("notaryAgents_list", sortByName(td,"name"));
        cb && cb();
    }
};

