export const sortByName = (td, key) =>
callbackOnMemory(
	"sortByName",
	[td, key],
	() => {
        let order = true;
		let sortedA = td && td.sort((a, b) => {
			let A = a[key];
			let B = b[key];
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);


