import React, { useEffect, useState, useRef } from "react";



import { getStore, gen16CodeId, ObjectKeys,  parseDate, updStore, _monthNames, copyToClipboard, parseDateMMDDYYYY, fetchPostH, getUrlServerSide } from "../../../stores/Utils";



import "../component/dashboard.css"
import { OpenModal } from "../../../components/DialogHRM";
import AddChildrens from "./ChildrensAdd";
import { SvgIcon_Cmpt } from "../../../components";
import UpdChildrens from "./ChildrensUpdate";
import DeleteAlert from "../component/deleteAlert";
import { getById_notary } from "../services/fetch_service";
import { PickClient_Cmpt } from "..";

const Svg = SvgIcon_Cmpt();

const PickClient = PickClient_Cmpt();




const ChildrensDashboard  = ({ openUpd,   close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);


	const updObs =  ()=>{
		set_obs(gen16CodeId())
	}
	

	const addChildrens = async (v)=>{
		let ffm = {...getStore("upd_client")};

		let childrens = ffm?.childrens || [];
		if(v.clientNotaryId){
			childrens.push(v.clientNotaryId)
			

			let bdyq2 = {
				query: "updateClientNotary",
				params:{
					"clientNotaryId": ffm["clientNotaryId"]
				},
				data2update: {childrens }
			};

			// console.log(bdyq2)
			
			/*  */
			const res = fetchPostH(getUrlServerSide(), bdyq2);
			const td = await res;
			if (td && td.data) {
				fetchClientData();
			}
		}
		
	}


	
	const rmvChildrens = async (indX2)=>{
		let ffm = {...getStore("upd_client")};

		let childrens = ffm?.childrens || [];
		childrens.splice(indX2,1);


		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {childrens }
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			fetchClientData();
		}
		
	}


	const openUpdView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<AddChildrens confirm={addChildrens} />
		OpenModal(modal_data);
	}

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			// let user = {...getStore("upd_client")};
			set_obs(gen16CodeId())
		}
	});


	
	const fetchClientData =  ()=>{
		getById_notary(getStore("upd_client")?.clientNotaryId,updObs)
	}

	const openPick = (f)=>{
		let data = {};
		data["zIndex"] = 480;
		// data['observeResize']=true;
		data["props"] = { minHeight: "1vh", overlay: true };
		data["content"] = <PickClient handleClick={addChildrens} />
		OpenModal(data);
	}

	

	let list = getStore("upd_client")?.childrensInfo || [];

	return (
		<div className=" ">
		
			<div className={"detailClient"}>
				<div className="_dsplFlx ">
					<p className="form_title">
						Hijos
					</p>
					<div className="flexSpace"/>
					<p className="group_title addBtn" onClick={openPick}>
						Agregar Hijos
					</p>
				</div>
			</div>
				
			<div className=" ">
				{list.map((item2, indX2)=> 
					<ChildrensItem  
						item={item2} 
						key={indX2}
						index={indX2}
						deleteJob={rmvChildrens}
					/>
				)}
			</div>
		</div>
	);
};

export default ChildrensDashboard;







const ChildrensItem  = ({ item,   updChildrens, index, deleteJob }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const updObs  =  (v)  => {
		set_obs( gen16CodeId());
	};

	const copyToClpb  =  (v)  => {
		copyToClipboard( v,updObs);
	};

	const _updChildrens  =  (v)  => {
		updChildrens(v,index)
	};

	const openUpdView =  () => {

		updStore("upd_jobs", item);
		updStore("upd_jobs_bck", JSON.parse(JSON.stringify(item)));
		

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<UpdChildrens confirm={_updChildrens} />
		OpenModal(modal_data);
	}

	const confirmDelete =  (v)  => {
		deleteJob(index)
	};



	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert 
				confirm={confirmDelete}
				lbl_p={"Eliminar Empleo"} 
				lbl_span={"Una vez eliminado no podra ser recuperado"}
			/>
			OpenModal(modaldata);
		}
	}


	return (

		<div className="_dsplFlx ">
			<div className="flexSpace"/>
			<div className={`card_address modern `}  >
				<div className="_dsplFlx ">
					<div className="title_card"  onClick={()=>copyToClpb(item.firstName)}>
							{item.firstName}
					</div>
					<div className="title_card"  onClick={()=>copyToClpb(item.middleName)}>
							{item.middleName}
						</div>
					<div className="flexSpace"/>
					<div className={"icon_action _dsplFlx"}>
						<div className={"menu_icon _dsplFlx"}> 
							<div className="flexSpace"/>
							<Svg 
							name={"more_vert"}	/>
						</div>
						<div className={"menu_icon_acc _dsplFlx"}>
							<div className="flexSpace"/>
							<div 	onClick={OpenDeleteAlert}>
								<Svg name={"outline_delete"}	/>
							</div>
						</div>
					</div>
				</div>
				<div className="body_card">
					<div className="_dsplFlx city_state">
						
						<div className="h4_gsans" onClick={()=>copyToClpb(item.lastName)}>
							{item.lastName}
						</div>
					</div>
					{item.dateOfBirth?
					<div className="_dsplFlx city_state">
						<div className="h5_gsans"  onClick={()=>copyToClpb(parseDateMMDDYYYY(item.dateOfBirth))}>
							{parseDate(item.dateOfBirth)}
						</div>
					</div>
					:null}
				</div>
				<div className="body_card">
					
					<div className="_dsplFlx city_state">
						<div className="flexSpace"/>
						{item?.placeOfBirth?.city?
						<div className="h4_gsans"  onClick={()=>copyToClpb(item?.placeOfBirth?.city)}>
							{item?.placeOfBirth?.city}
						</div>
						:null}
						{item?.placeOfBirth?.state?
						<div className="h4_gsans" onClick={()=>copyToClpb(item?.placeOfBirth?.state)}>
							{item?.placeOfBirth?.state}
						</div>
						:null}
					</div>
				</div>
			</div>
			<div className="flexSpace"/>
		</div>
	)
}











const compareJSON = (fls,obj, bkUp) =>{
	let r = 0;
	let data = {}
	for(let i in fls){
		let k1 = fls[i];
		if(obj[k1] ){
			if(typeof obj[k1] === "object"){
				ObjectKeys(obj[k1]).map(fk2=>{
					if(obj?.[k1]?.[fk2]!==bkUp?.[k1]?.[fk2]){
						r = 1;
						data[k1] = obj[k1];
					}
				})
			}
			else if(obj[k1] &&  obj[k1]!==bkUp[k1]){
				r = 1;
				data[k1] = obj[k1];
			}
		}
	}
	return {data, hasChange:r};
  }



